/* Regular */
@font-face {
  font-family: 'TTLakes';
  font-style: normal;
  font-weight: 400;
  src: url(./fonts/TTLakes-Regular.ttf) format("ttf");
  src: url(./fonts/TTLakes-Regular.woff) format("woff"); }

/* Bold */
@font-face {
  font-family: 'TTLakes-bold';
  font-style: normal;
  font-weight: 800;
  src: url(./fonts/TTLakes-Bold.ttf) format("ttf");
  src: url(./fonts/TTLakes-Bold.woff) format("woff"); }

@font-face {
  font-family: 'TT Lakes Compressed Bold';
  font-style: normal;
  font-weight: 800;
  src: url(./fonts/TTLakesCompressed-Bold.ttf) format("ttf");
  src: url(./fonts/TTLakesCompressed-Bold.woff) format("woff"); }

@font-face {
  font-family: 'TT Lakes Compressed Medium';
  font-style: normal;
  font-weight: 400;
  src: url(./fonts/TTLakesCompressed-Medium.ttf) format("ttf");
  src: url(./fonts/TTLakesCompressed-Medium.woff) format("woff"); }

body {
  padding: 0rem;
  margin: 0;
  font-family: 'TTLakes', sans-serif, 'TT Lakes Compressed Bold';
  background-color: #fff;
  color: #1B1C21; }
