/*====================mobile menu========================*/

.mobile-menu {
	position: absolute;
	display:flex;
	/* display: none;*/
	flex-direction: column;
	left: 0;
	top: 0;
	background-color: #F8F8F8;
	padding: 1rem 1.5rem 2rem 1.5rem;
	opacity: 1;
	width: 320px;
	height: 100%;
	z-index: 10;
	box-shadow: 16px 0 52px 0 rgba(14, 24, 80, 0.2);
	font-family:  'TTLakes',sans-serif;
	font-size: 16;
	z-index: 100;
}

@media (min-width:1439.98px) {
	.mobile-menu {
		display: flex;
		position: fixed;
		min-width: 305px;
		height: 100vh;
		background-color: #fff;
		box-shadow: 16px 0 52px 0 rgba(14, 24, 80, 0);
	}
}
@media (max-width:1439.97px) {
	.mobile-menu {
		top: 0;
		left: 0;
	}
}

@media (max-width: 1439.98px) {
	.mobile-menu--initial{
		/*transform: translateX(0px);*/
		/*left: 0;*/
		left: -321px;
		transform: translateX(0px);
	}
	
}

.mobile-menu__header {
	display: flex;
	align-items: center;
}

.mobile-menu__left-section-header {
	display: flex;
	flex: 1 1 auto;
	align-items: center;
}
.mobile-menu__left-section-header button:hover {
	transform: scale(1.1);
}
.mobile-menu__search:hover{
	transform: scale(1.1);
}

.mobile-menu__close-button {}

@media (min-width: 1439.98px) {
	.mobile-menu__close-button {
		display: none;
	}
}

.close-button {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	background-color: #FF3E79;
	position: relative;
	margin-right: 1rem;
}

.close-button svg {
	background: url(./static/close.svg);
	background-repeat: no-repeat;
	display: inline-block;
	position: absolute;
	top: 28%;
	left: 28%;

}

.close-button__text {
	display: none;
}

.mobile-menu__logo {}

/*=======mobile menu list====================*/
.mobile-menu__nav{
	flex: 1 1 auto;
	
}
.mobile-menu__nav-list {
	
	margin: 3rem 0 0 0;
	color: #000000;
}

.mobile-menu__nav-item:not(:last-child) {
	margin: 0 0 2rem 0;
}

.mobile-menu__nav-link {
	color: black;
}

.mobile-menu__nav-link--active {
	color: #7E7E82;
}
.mobile-menu__nav-item{
	position: relative;
}
.mobile-menu__nav-item--active::before {
	content: "";
	position: absolute;
	height: 32px;
	width: 25px;
	background: #41F6D7;
	
	border-radius: 25%;
	left: -46px;
	top: -8px;
}

/*=============mobile menu footer ====================*/
.mobile-menu__footer-mobile {
	display: flex;
	flex-direction: column;
	flex: 0 0 auto;
}

.footer-mobile {
	font-family: 'TTLakes-Bold';
}

.footer-mobile__icons-container {
	display: flex;
	gap: 1rem;
	margin-bottom: 1rem;
}
.footer-mobile__icons-container button:hover {
	transform: scale(1.1);
}


.footer-mobile__mail {
	font-size: 18px;
	color: #1B1C21;
	letter-spacing: -0.2px;
	position: relative;
}
.footer-mobile__mail::before {
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;
	padding: 0 0;
	max-width: 0;
	border-bottom: 2px solid rgb(0, 0, 0);
	color: #41f6d8b3;
	content: attr(data-hover);
	transition: max-width 0.5s;
	white-space: nowrap;
}
.footer-mobile__mail:hover::before,
.footer-mobile__mail:focus::before{
	max-width: 100%;
}



.footer-mobile__tel {
	position: relative;
	display: inline-block;
	font-weight: 600;
	font-size: 24px;
	letter-spacing: -0.6px;
	text-align: left;
	margin: 1rem 0 3rem 0;
	align-self: flex-start;
}
.footer-mobile__tel::before {
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;
	padding: 0 0;
	max-width: 0;
	border-bottom: 2px solid rgb(0, 0, 0);
	color: #41f6d8b3;
	content: attr(data-hover);
	transition: max-width 0.5s;
	white-space: nowrap;
}
.footer-mobile__tel:hover::before,
.footer-mobile__tel:focus::before{
	max-width: 100%;
}


.footer-mobile__languages {
	display: flex;
}

.footer-mobile__languages a {
	font-size: 18px;
	color: #1B1C21;
}
.footer-mobile__languages a:hover {
	transform: scale(1.1);
}

.footer-mobile__languages .footer-mobile__ru--active {
	color: #7E7E82;
}

.footer-mobile__languages a:not(:last-child) {
	margin-right: 0.4rem;
}

/*hover */
.mobile-menu__nav-list li a{
	display: block;
}
.mobile-menu__nav-list li a:hover{
	transform-origin:0  0 ;
	transform: scale(1.1);
}
