/* order-call form */
.order-call {
	display: flex;
	box-shadow: -16px 0 52px 0 rgba(14, 24, 80, 0.2);
	position: absolute;
	z-index: 100;
}

@media (max-width:1439.98px) {
	.order-call--initial {
		right: -382px;
		transform: translateX(0px);
	}
}
@media (min-width:1439.9px) {
	.order-call--initial {
		right: -400px;
		transform: translateX(0px);
	}
}



.order-call__close-btn {
	margin: 1rem;
	
}
@media (min-width:1439.98px) {
	.order-call__close-btn {
		position: absolute;
		top: -10px;
		left: -85px;
		
	}
}

